<template>
  <div>
    <el-date-picker v-model="date" type="date" value-format="yyyy-MM-dd" format="yyyy-MM-dd" style="margin-bottom: 30px;margin-right: 10px"> </el-date-picker>
    <el-button type="primary" @click="dateFilter">筛选</el-button>
    <el-table :data="recordForm" :header-cell-style="{background:'#eef1f6',color:'#606266'}"
               height="400px" :row-class-name="tableRowClassName" border>
      <el-table-column prop="id" label="编号" width="50px"></el-table-column>
      <el-table-column prop="sno" label="学号"></el-table-column>
      <el-table-column prop="healthqrcode" label="健康码状态"></el-table-column>
      <el-table-column prop="tripqrcode" label="行程码状态"></el-table-column>
      <el-table-column prop="address" label="地址" width="250px"></el-table-column>
      <el-table-column prop="date" label="填报日期" width="250px"></el-table-column>
    </el-table>
  </div>
</template>
<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
.el-table .error-row {
  background: red;
}
</style>
<script>
import Axios from "axios";

export default {
  inject: ['reload'],
  data() {
    return {
      recordForm: [],
      cno: '',
      date: ''
    }
  },
  created:
      function () {
        this.date = new Date().getFullYear()+'-'+ (new Date().getMonth()+1)+'-'+ new Date().getDate();
        let _this = this;
        Axios.get("http://localhost:8080/teacher/get_cno/" + this.$store.state.id)
            .then(function (response) {
              _this.cno = response.data;
              Axios.get("http://localhost:8080/record/show_class_record/" + _this.cno)
                  .then(function (response_form) {
                    _this.recordForm = response_form.data;
                  });
            });
      },
  methods: {
    tableRowClassName({row, rowIndex}) {
      rowIndex.title;
      if (row.healthqrcode === '红色' || row.tripqrcode === '红色') {
        return 'error-row';
      } else if (row.healthqrcode === '黄色' || row.tripqrcode === '黄色') {
        return 'warning-row';
      }
      return '';
    },
    dateFilter() {
      let _this = this;
      Axios.get("http://localhost:8080/record/show_date_record/"+this.date)
      .then(function (response){
        _this.recordForm = response.data;
      })
    }
  }
}
</script>