<template>
  <div style="max-width: 20%; min-width: 200px; margin: 0 auto">
    <el-row style="text-align: center">
      <h1>登陆</h1>
    </el-row>
    <el-form label-width="80px" :model="loginForm" :rules="rules" ref="loginForm">
      <el-form-item label="学号" prop="id">
        <el-input placeholder="请输入学号或者教师号" v-model="loginForm.id"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input placeholder="请输入密码(默认为学号后六位)" v-model="loginForm.password" show-password></el-input>
      </el-form-item>
      <el-form-item prop="userType" label="用户类型">
        <el-radio v-model="loginForm.userType" :label="1">学生</el-radio>
        <el-radio v-model="loginForm.userType" :label="2">教师</el-radio>
      </el-form-item>
      <el-row type="flex" justify="space-around">
        <el-button :span="6" type="primary" style="width: 100%" @click="loginSubmit('loginForm')"
                   icon="el-icon-s-promotion">登陆
        </el-button>
        <el-button :span="6" type="primary" style="width: 100%" icon="el-icon-edit" @click="goRegisterPage" plain>
          修改密码
        </el-button>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import Axios from "axios";
import Message from "element-ui/packages/message/src/main";

export default {
  data() {
    return {
      rules: {
        id: {required: true, message: '请输入学号或教师号', trigger: 'change'},
        userType: {required: true, message: '请选择用户类型', trigger: 'blur'}
      },
      loginForm: {
        id: '',
        password: '',
        userType: '',
      }
    }
  },
  methods: {
    goRegisterPage() {
      this.$router.push({path: '/change_pwd'})
    } ,
    loginSubmit(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          let _this = this;
          let url;
          let base_url = "http://localhost:8080";
          if (this.loginForm.userType === 1)
            url = base_url + "/student";
          else
            url = base_url + "/teacher";
          Axios.post(url + "/check_pwd/" + _this.loginForm.id + "&" + _this.loginForm.password)
              .then(function (response) {
                // console.log(response);
                if (response.data === -1) {
                  Message({
                    message: '无此用户',
                    type: 'warning'
                  })
                } else if (response.data === 1) {
                  Message({
                    message: '登陆成功',
                    type: 'success'
                  });
                  Axios.get(url + "/get_name/" + _this.loginForm.id)
                      .then(function (response_name) {
                        _this.$store.dispatch("setUserNameAysnc", response_name.data);
                      });
                  if (_this.loginForm.userType === 1)
                    _this.$router.push({path: '/show_record'});
                  else
                    _this.$router.push({path: '/teacher_index'});
                  _this.$store.dispatch("setIdAysnc", _this.loginForm.id);
                } else if (response.data === 0) {
                  Message({
                    message: '密码错误',
                    type: 'warning'
                  })
                }
              })
              .catch(function (err) {
                Message({
                  message: '请求错误，原因：' + err,
                  type: 'error'
                })
              })
        }
      });
    }
  }
}

</script>

