import Vue from 'vue'
import Router from 'vue-router'

//组件
import login from '../components/Login'
import change_pwd from '../components/ChangePwd'
import show_record from '../components/RecordShow'
import add_record from '../components/RecordAdd'
import teacher_index from '../components/teacherIndex'
import student_manage from '../components/StudentManage'
import record_manage from '../components/RecordManage'

Vue.use(Router)

const routes = [
    {
        path: '/',
        name: 'login',
        component: login
    },
    {
        path: '/login',
        name: 'login',
        component: login
    },
    {
        path: '/change_pwd',
        name: 'change pwd',
        component: change_pwd
    },
    {
        path: '/show_record',
        name: 'show record',
        component: show_record
    },
    {
        path: '/add_record',
        name: 'add record',
        component: add_record
    },
    {
        path: '/teacher_index',
        name: 'teacher index',
        component: teacher_index,
        //    教师页面子路由
        children: [
            {
                path: 'student_manage',
                name: 'student manage',
                component: student_manage
            },
            {
                path: 'record_manage',
                name: 'record manage',
                component: record_manage
            }
        ]
    }
];


export default new Router({
    mode: 'history',
    routes
})