<template>
  <div style="max-width: 20%; min-width: 200px; margin: 0 auto">
    <el-row style="text-align: center">
      <h1>修改密码</h1>
    </el-row>
    <el-form label-width="80px" :model="registerForm" :rules="rules" ref="registerForm">
      <el-form-item label="学号" prop="id">
        <el-input v-model="registerForm.id" placeholder="请输入学号"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="registerForm.password" placeholder="请输入密码" show-password></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="confirm_password">
        <el-input v-model="registerForm.confirm_password" placeholder="再次输入密码" show-password></el-input>
      </el-form-item>
      <el-row type="flex" justify="space-around">
        <el-button :span="6" type="primary" style="width: 100%" @click="formSubmit('registerForm')">
          确认修改
        </el-button>
        <el-button :span="6" type="primary" style="width: 100%" @click="goLoginPage" plain>
          返回登陆
        </el-button>
      </el-row>
    </el-form>
  </div>
</template>

<script>

import Axios from "axios";
import Message from "element-ui/packages/message/src/main";

export default {
  data() {
    let validatePwd = (rule, value, callback) => {
      if (value !== this.registerForm.password)
        callback(new Error('两次输入的密码不一致'));
      else
        callback();
    };
    return {
      rules: {
        id: {required: true, message: "请输入学号", trigger: 'blur'},
        confirm_password: {validator: validatePwd, trigger: 'blur', required: true}
      },
      registerForm: {
        id: '',
        password: '',
        confirm_password: ''
      }
    }
  },
  methods: {
    goLoginPage() {
      this.$router.push({path: '/login'})
    },
    formSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _this = this;
          let url = "http://localhost:8080/student/change_pwd/" + this.registerForm.id + "&" + this.registerForm.password;
          Axios.post(url).then(function (response) {
            let res = response.data;
            console.log(response);
            if (res === 1) {
              Message({
                message: '修改密码成功',
                type: 'success'
              });
              _this.$router.push({path: '/login'})
            } else {
              Message({
                message: '学号错误，无此学号',
                type: 'error'
              })
            }

          })
              .catch(function (err) {
                Message({
                  message: '请求错误，原因：' + err,
                  type: 'error'
                })
              })
        } else {//校验不通过
          return false;
        }
      });
    }
  }
}

</script>

