<template>
  <div>
    <el-table :data="studentForm" :header-cell-style="{background:'#eef1f6',color:'#606266'}" height="400px" border>
      <el-table-column prop="sno" label="学号"></el-table-column>
      <el-table-column prop="sname" label="姓名"></el-table-column>
      <el-table-column prop="password" label="密码"></el-table-column>
      <el-table-column prop="lastfillingtime" label="上次填报时间"></el-table-column>
    </el-table>
    <el-button icon="el-icon-circle-plus" style="margin-top: 20px" @click="dialogFormVisible=true">
      添加学生
    </el-button>
    <el-dialog title="添加学生信息" :visible.sync="dialogFormVisible">
      <el-form :model="dialogForm" :rules="rules" ref="dialogForm">
        <el-form-item label="学号" prop="sno">
          <el-input v-model="dialogForm.sno" placeholder="请输入学生学号"></el-input>
        </el-form-item>
        <el-form-item label="学生姓名" prop="sname">
          <el-input v-model="dialogForm.sname" placeholder="请输入学生姓名"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" :loading="isloading" @click="addStudent('dialogForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Axios from "axios";
import Message from "element-ui/packages/message/src/main";

export default {
  inject: ['reload'],
  data() {
    return {
      cno: '',
      dialogFormVisible: false,
      studentForm: [],
      dialogForm: {
        sno: '',
        sname: ''
      },
      rules: {
        sno: [{required: true, message: '请输入学生学号', trigger: 'blur'},{validator: this.checkInput, trigger: 'blur'}],
        sname : {required: true, message: '请输入学生姓名', trigger: 'blur'}
      },
      isloading: false
    }
  },
  created:
      function () {
        let _this = this;
        Axios.get("http://localhost:8080/teacher/get_cno/" + this.$store.state.id)
            .then(function (response) {
              _this.cno = response.data;
              Axios.get("http://localhost:8080/student/show_by_cno/" + _this.cno)
                  .then(function (response_form) {
                    _this.studentForm = response_form.data;
                  });
            });
      },
  methods: {
    checkInput(rule, input,callback) {
      let regex = /[0-9]{10}/;
      if(!regex.test(input))
        callback(new Error('学号必须是十位数'));
      else
        callback();
    },
    addStudent(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          let _this = this;
          _this.isloading = true;
          Axios.post("http://localhost:8080/student/add/" + this.dialogForm.sno + "&" + this.dialogForm.sname + "&" + this.cno)
              .then(function (response) {
                if (response.data === true) {
                  Message({
                    message: '添加成功',
                    type: 'success'
                  });
                } else {
                  Message({
                    message: '添加失败',
                    type: 'error'
                  })
                }
              });
          _this.isloading = false;
          _this.dialogFormVisible = false;
          _this.reload();
        }
      });
    }
  }
}

</script>