<template>
  <div style="max-width: 40%; min-width: 200px; margin: 0 auto">
    <el-row style="text-align: center">
      <h1>健康填报</h1>
    </el-row>
    <el-row style="text-align: center">
      <h2>欢迎你，{{ $store.state.username }}同学</h2>
    </el-row>
    <el-row>
      <el-button type="primary" @click="addClick">
        新增
      </el-button>
      <el-button type="primary" @click="$router.push({path: '/'});$store.state.id=''" plain>
        登出
      </el-button>
    </el-row>
    <el-table height="500" :data="tableData" border :header-cell-style="{background:'#eef1f6',color:'#606266'}"
              style="margin-top: 10px">
      <el-table-column label="日期" prop="date" width="200px" sortable>
      </el-table-column>
      <el-table-column label="学号" prop="sno" width="100px">
      </el-table-column>
      <el-table-column label="健康码" prop="healthqrcode" width="100px">
      </el-table-column>
      <el-table-column label="行程码" prop="tripqrcode" width="100px">
      </el-table-column>
      <el-table-column label="地址" prop="address">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Axios from "axios";
import Message from "element-ui/packages/message/src/main";

export default {
  data() {
    return {
      tableData: []
    }
  },
  mounted: function () {
    if(this.$store.state.id==='') {
      Message({
        message: '请先登录账户',
        type: 'error'
      });
      this.$router.push({path: '/'});
      return ;
    }
    let url = "http://localhost:8080/record/show_student_record/" + this.$store.state.id;
    let _this = this;
    Axios.get(url).then(function (response) {
      console.log(response.data);
      _this.tableData = response.data;
    })
        .catch(function (err) {
          Message({
            message: '获取健康信息错误，原因：' + err,
            type: 'error'
          })
        })
  },
  methods: {
    addClick() {
      let url = "http://localhost:8080/student/check/" + this.$store.state.id;
      let _this = this;
      Axios.get(url).then(function (response) {
        if(response.data === true)
          _this.$router.push({path: '/add_record'})
        else {
          Message({
            message: '今天已经填报过了',
            type: 'warning'
          });
        }
      }).catch(function (err) {
        Message({
          message: err,
          type: 'error'
        })
      })
    },
    cancelForm() {
      this.drawer = false;
    }
  }
}
</script>