<template>
  <div style="max-width: 55%; min-width: 200px; margin: 0 auto; text-align: center">
    <el-page-header content="信息管理" @back="goback"></el-page-header>
    <h2>欢迎你，{{ $store.state.username }}老师</h2>
    <el-radio-group v-model="radioValue" @change="radioClick" style="margin-bottom: 30px">
      <el-radio-button label="学生管理"></el-radio-button>
      <el-radio-button label="健康填报管理"></el-radio-button>
    </el-radio-group>
    <router-view v-if="isRouterAlive"/>
  </div>
</template>

<script>
import Message from "element-ui/packages/message/src/main";

export default {
  provide() {
    return {
      reload: this.reload
    }
  },
  data() {
    return {
      radioValue: '学生管理',
      isRouterAlive: true
    }
  },
  created:
      function () {
        if (this.$store.state.id === '') {
          Message({
            message: '请先登录账户',
            type: 'error'
          });
          this.$router.push({path: '/'});
        } else if (this.radioValue === '学生管理')
          this.$router.push({path: '/teacher_index/student_manage'});
      }
  ,
  methods: {
    radioClick: function (val) {
      if (val === '学生管理')
        this.$router.push({path: '/teacher_index/student_manage'});
      else if (val === '健康填报管理')
        this.$router.push({path: '/teacher_index/record_manage'});
    },
    goback: function (){
      this.$router.push({path: '/'});
      this.$store.state.username = '';
      this.$store.state.id = '';
    },
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function (){
        this.isRouterAlive = true;
      });
    }
  }
}
</script>