<template>
  <div style="max-width: 50%; min-width: 200px; margin: 0 auto">
    <el-alert center show-icon title="为了您和家人的平安，请您如实填报每日的身体状况信息，以免因为错误的数据没有得到有效的监测而错过了最佳的医治时期。"
              type="warning" :closable="false"></el-alert>
    <el-form style="margin-top: 10px" :model="recordForm" :rules="rules" ref="recordForm">
      <el-row>
        <el-form-item label="健康码颜色" prop="healthQRCode">
          <el-select placeholder="请选择健康码颜色" v-model="recordForm.healthQRCode">
            <el-option label="绿色" value="绿色"></el-option>
            <el-option label="红色" value="红色"></el-option>
            <el-option label="黄色" value="黄色"></el-option>
          </el-select>
        </el-form-item>
      </el-row>
      <el-row>
        <el-form-item label="行程码颜色" prop="tripQRCode">
          <el-select placeholder="请选择行程码颜色" v-model="recordForm.tripQRCode">
            <el-option label="绿色" value="绿色"></el-option>
            <el-option label="红色" value="红色"></el-option>
            <el-option label="黄色" value="黄色"></el-option>
          </el-select>
        </el-form-item>
      </el-row>
      <el-row>
        <el-form-item label="地址" label-width="82px" prop="address">
          <el-cascader separator=" " @change="changeCasc" v-model="recordForm.address" :options="regionData"
                       placeholder="请选择地址"></el-cascader>
        </el-form-item>
      </el-row>
      <el-row type="flex" justify="space_around">
        <el-button :span="6" @click="submit('recordForm')" type="primary">提交</el-button>
        <el-button :span="6" @click="$router.push({path:'/show_record'})">返回</el-button>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {
  regionData,
  CodeToText
} from 'element-china-area-data'
import Axios from "axios";
import Message from "element-ui/packages/message/src/main";

export default {
  data() {
    return {
      rules: {
        healthQRCode: {required: true, message: '请选择健康码颜色', trigger: 'change'},
        tripQRCode: {required: true, message: '请选择行程码颜色', trigger: 'change'},
        address: {required: true, message: '请选择地址', trigger: 'change'}
      },
      CodeToText,
      regionData,
      recordForm: {
        healthQRCode: '',
        tripQRCode: '',
        address: ''
      }
    }
  },
  created: {
    function() {
      if (this.$store.state.id === '') {
        Message({
          message: '请先登录账户',
          type: 'error'
        });
        this.$router.push({path: '/'});
      }
    }
  },
  methods: {
    changeCasc() {
      console.log(this.recordForm.address)
    },
    submit(form) {
      if(this.$store.state.id==='') {
        Message({
          message: '请先登录账户',
          type: 'error'
        });
        this.$router.push({path: '/'});
        return ;
      }
      this.$refs[form].validate((valid) => {
        if (valid) {
          let tmp = this.recordForm.address
          let adr = CodeToText[tmp[0]] + CodeToText[tmp[1]] + CodeToText[tmp[2]];
          let url = "http://localhost:8080/record/add/" + this.$store.state.id + "&"
              + this.recordForm.healthQRCode + "&"
              + this.recordForm.tripQRCode + "&"
              + adr;
          let _this = this;
          Axios.post(url).then(function (response) {
            if (response.data === true) {
              Message({
                message: '今日填报完成～',
                type: 'success'
              });
              _this.$router.push({path: "/show_record"})
            } else {
              Message({
                message: '填报失败, 今日已填报过',
                type: 'error'
              });
              _this.$router.push({path: '/show_record'})
            }
          }).catch(function (err) {
            Message({
              message: err,
              type: 'error'
            })
          })
        }
      })
    }
  }
}

</script>